<template>
    <section-block title="Мои желания" :buttons="[{name: 'Добавить желание', url: '/wishes/create', icon: 'fa-plus-square-o'}]" :class="[currentWish ? 'mess-hidden': 'mess-visible']">
        <div class="wish" v-for="(wish, key) in wishes" :class="{active: currentWish == key}" @click="currentWish = key">
            <div class="wish-budget pull-right">
                    <span>
                        <template v-if="wish.budget">{{wish.budget}} ₽</template>
                        <template v-else>Не указан</template>
                    </span>
            </div>
            <div class="wish-title">{{wish.title}}</div>
            <div class="wish-description">{{wish.description}}</div>
        </div>
    </section-block>
</template>

<script>
    import sectionBlock from '../global/section.vue'

    import { mapState, mapGetters } from 'vuex'

    export default {
        name: 'wishes',
        props: ['selected'],
        data() {
            return {
                currentWish: 0,
                wishes: {
                    1: {
                        id: 1,
                        title: 'Тест',
                        description: 'Тестирование описания желания',
                        budget: 1000,
                    },
                    2: {
                        id: 1,
                        title: 'Тестирование',
                        description: 'Тестирование описания желания, очень объемного описания, которое должно выходить за пределы блока. Тестирование.',
                        budget: 0,
                    },
                    3: {
                        id: 1,
                        title: 'Тестовое желание',
                        description: 'Тестирование описания желания',
                        budget: 3000,
                    },
                }
            }
        },
        components: {
            sectionBlock
        },
        computed: {
            ...mapState(['auth']),
            ...mapGetters(['fullname'])
        },
        methods: {

        },
        watch: {
            selected(id) {
                this.currentWish = id;
            },
            currentWish(id) {
                this.$emit('setWish', id);
            }
        }
    }
</script>

<style lang="scss">
    .wish {
        padding: 10px;
        border-bottom: 1px solid #f1f1f1;
        cursor: pointer;
        transition: background-color .3s;

        &:hover {
             background-color: #fafafa;
         }

        &.active {
             background-color: aliceblue;
         }

        .wish-title {
            color: #3c8dbc;
            font-size: 16px;
            font-weight: 400;
            line-height: 18px;
        }
        .wish-description {
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;
            margin-top: 10px;
        }
        .wish-budget {
            color: white;
            font-weight: 400;
            background-color: #3c8dbc;
            border-radius: 3px;
            padding: 2px 5px;
        }
    }
</style>


