<template>
    <div class="row">
        <div class="col-lg-12">
            <router-view @setWish="setWish" :selected="currentWish"></router-view>
        </div>
        <div class="col-lg-12" :class="[currentWish ? 'mess-visible': 'mess-hidden']">
            <section-block title="Поиск исполнителя">
                <div class="performers-list" v-if="currentWish">
                    <div class="back-line hidden-xxl hidden-xl hidden-lg" @click="currentWish = 0">
                        <i class="fa fa-chevron-left"></i>
                        Назад
                    </div>
                    <div class="performer" v-for="(performer, key) in performers">
                        <button class="btn btn-success pull-right">Выбрать<span class="hidden-zero hidden-xs hidden-sm"> исполнителя</span></button>
                        <router-link :to="'/profile/' + performer.id">
                            <div class="avatar pull-left">
                                <img :src="performer.avatar" alt="ava" class="img-circle">
                            </div>
                            <div class="performer-info">
                                <div class="fullname">
                                    {{performer.firstname}} {{performer.lastname}}
                                </div>
                                <div class="rating">
                                    <el-rate v-model="performer.rate"
                                             disabled
                                             text-color="#ff9900"></el-rate>
                                </div>
                            </div>
                        </router-link>
                    </div>
                </div>
                <span v-else class="acenter noData">Для просмотра исполнителей выберите желание из списка слева</span>
            </section-block>
        </div>
    </div>
</template>

<script>
    import sectionBlock from '../global/section.vue'

    import { mapState, mapGetters } from 'vuex'

    export default {
        name: 'wishes',
        data() {
            return {
                currentWish: 0,
                performers: [
                    {
                        id: 2,
                        avatar: '/images/avatarDF.png',
                        firstname: 'Дмитрий',
                        lastname: 'Федоров',
                        rate: 3.5
                    },
                    {
                        id: 3,
                        avatar: '/images/avatarAC.jpg',
                        firstname: 'Андрей',
                        lastname: 'Царенков',
                        rate: 2.5
                    },
                    {
                        id: 4,
                        avatar: '/images/avatarIF.jpg',
                        firstname: 'Игорь',
                        lastname: 'Федорец',
                        rate: 4
                    },
                ],
            }
        },
        components: {
            sectionBlock
        },
        computed: {
            ...mapState(['auth']),
            ...mapGetters(['fullname'])
        },
        methods: {
            setWish(id) {
                this.currentWish = id;
            }
        },
    }
</script>

<style lang="scss">
    .performers-list {
        .back-line {
            font-size: 12px;
            padding: 10px 7px;
            color: #3c8dbc;
            transition: background-color .3s;
            cursor: pointer;
            border-bottom: 1px solid #f1f1f1;

            &:hover {
                background-color: aliceblue;
            }

            i.fa {
                margin-right: 10px;
            }
        }
    }
    .performer {
        padding: 10px;
        border-bottom: 1px solid #f1f1f1;
        transition: background-color .3s;

        &:hover {
            background-color: #fafafa;
        }

        .avatar img {
            width: 50px;
            height: 50px;
        }
        button {
            margin-top: 7px;
        }
        .performer-info {
            margin-left: 65px;
            margin-top: 3px;
            .fullname {
                color: #3c8dbc;
                font-size: 15px;
                margin-bottom: 10px;
            }
        }
    }
</style>


