<template>
    <section-block title="Создание нового желания">
        <div class="form">
            <div class="form-line">
                <span class="input" :class="{'input-filled': form.title}">
                    <input class="input-field" type="password" v-model="form.title"/>
                    <label class="input-label">
                        <span class="input-label-content">Заголовок</span>
                    </label>
                </span>
            </div>
            <div class="form-line" :class="{'input-filled': form.description}">
                <span class="input">
                    <auto-text class="input-field" v-model="form.description" :autosize="{minRows: 3, maxRows: 10}"></auto-text>
                    <label class="input-label">
                        <span class="input-label-content">Подробное описание</span>
                    </label>
                </span>
            </div>
            <div class="form-line">
                <span class="input input-filled" style="margin-top: 10px;">
                    Изобравжения
                    <el-upload
                            style="margin-top: 10px;"
                            action="/upload/images/"
                            list-type="picture-card"
                            :file-list="form.images"
                            accept="image/*"
                            :on-preview="handlePictureCardPreview"
                            :on-remove="handleRemove">
                        <i class="fa fa-plus"></i>
                    </el-upload>
                </span>
            </div>
            <div class="form-line">
                <span class="input input-filled">
                    <input class="input-field" type="number" v-model="form.budget" min="0"/>
                    <i class="fa fa-plus input-number" @click="form.budget++"></i>
                    <i class="fa fa-minus input-number" @click="form.budget--"></i>
                    <label class="input-label">
                        <span class="input-label-content">Бюджет</span>
                    </label>
                </span>
            </div>
            <div class="form-line">
                <span class="input input-filled">
                    <el-checkbox v-model="form.range" style="margin-top: 10px;">Указать диапозон дат</el-checkbox>
                </span>
            </div>
            <div class="form-line">
                <span class="input" :class="{'input-filled': form.start_date}">
                    <mask-input class="input-field" type="text" v-model="form.start_date"
                                :mask="[/[0-3]/, /\d/, ' ', '/', ' ', /[0-1]/, /\d/, ' ', '/', ' ', /[1-2]/, /[9,0]/, /\d/, /\d/, ' ', /[0-2]/, /\d/, ':', /[0-5]/, /\d/]"
                                :guide="true"
                                placeholderChar="_"
                                placeholder="__ / __ / ____ __:__"></mask-input>
                    <label class="input-label">
                        <span class="input-label-content"><span v-if="form.range">Дата начала</span><span v-else>Дата</span></span>
                    </label>
                </span>
            </div>
            <div class="form-line" v-show="form.range">
                <span class="input" :class="{'input-filled': form.stop_date}">
                    <mask-input class="input-field" type="text" v-model="form.stop_date"
                                :mask="[/[0-3]/, /\d/, ' ', '/', ' ', /[0-1]/, /\d/, ' ', '/', ' ', /[1-2]/, /[9,0]/, /\d/, /\d/, ' ', /[0-2]/, /\d/, ':', /[0-5]/, /\d/]"
                                :guide="true"
                                placeholderChar="_"
                                placeholder="__ / __ / ____ __:__"></mask-input>
                    <label class="input-label">
                        <span class="input-label-content">Дата окончания</span>
                    </label>
                </span>
            </div>
            <div class="form-line acenter">
                <button class="btn btn-primary" @click="save">Создать</button>
                <button class="btn btn-default" @click="back">Назад</button>
            </div>
        </div>
    </section-block>
</template>

<script>
    import sectionBlock from '../global/section.vue'
    import autoText from '../global/auto-textarea.vue'
    import maskInput from 'vue-text-mask'

    import { mapState, mapGetters } from 'vuex'

    export default {
        name: 'wishCreate',
        data() {
            return {
                config: {},
                form: {
                    title: '',
                    description: '123',
                    budget: 0,
                    range: false,
                    start_date: '',
                    stop_date: '',
                    images: []
                }
            }
        },
        components: {
            sectionBlock,
            autoText,
            maskInput
        },
        computed: {
            ...mapState(['auth'])
        },
        methods: {
            save() {
                console.log('saved');
            },
            back() {
                this.$router.go(-1);
            },
            handleRemove(file, fileList) {
                console.log(file, fileList);
            },
            handlePictureCardPreview(file) {
                this.dialogImageUrl = file.url;
                this.dialogVisible = true;
            }
        }
    }
</script>


