<template>
    <textarea
            @input="handleInput"
            :style="textareaCalcStyle"
            :value="currentValue"
            ref="textarea"
    ></textarea>
</template>

<script>
    import calcTextareaHeight from './auto-textarea/calcTextareaHeight';

    export default {
        name: 'autoTextarea',

        componentName: 'autoTextarea',

        data() {
            return {
                currentValue: this.value,
                textareaCalcStyle: {}
            };
        },

        props: {
            value: [String, Number],
            autosize: {
                type: [Boolean, Object],
                default: true
            }
        },

        watch: {
            'value'(val, oldValue) {
                this.setCurrentValue(val);
            }
        },

        methods: {
            resizeTextarea() {
                if (this.$isServer) return;
                const minRows = this.autosize.minRows;
                const maxRows = this.autosize.maxRows;

                this.textareaCalcStyle = calcTextareaHeight(this.$refs.textarea, minRows, maxRows);
            },
            handleInput(event) {
                const value = event.target.value;
                this.$emit('input', value);
                this.setCurrentValue(value);
                this.$emit('change', value);
            },
            setCurrentValue(value) {
                if (value === this.currentValue) return;
                this.$nextTick(_ => {
                    this.resizeTextarea();
                });
                this.currentValue = value;
            }
        },

        mounted() {
            this.resizeTextarea();
        }
    };
</script>


