<template>
    <section>
        <div class="title" v-if="title">
            {{title}}
            <div class="button" v-for="button in buttons">
                <router-link :to="button.url"><i class="fa" :class="button.icon"></i> {{button.name}}</router-link>
            </div>
        </div>
        <slot>Отсутствует содержимое блока</slot>
    </section>
</template>

<script>
    export default {
        name: 'sectionBlock',
        props: {
            title: {
                type: String,
                default: ''
            },
            buttons: {
                type: Array,
                default: function () {
                    return []
                }
            }
        }
    }
</script>


